import { navigate } from "gatsby"
import React, { useState } from 'react';

import styled from 'styled-components';

const NavContainer = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255);
  width: 100%;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}
`

const List = styled.ul`
  display: flex;
  margin: 0;
  box-shadow: 0 4px 2px -2px rgba(12,13,14,0.15);
  width: 100vw;
  align-items: center;
  justify-content: center;

  @media (min-width: 992px) {
    width: 65vw;
    justify-content: flex-start;
  }
`

const Item = styled.li`
  margin: 0;  
  padding: 0.75rem 1rem 0.45rem;
  list-style: none;
  transition: 0.65s ease-out;
  border-bottom: 5px solid transparent;
  color: #2b2b2c;
  cursor: pointer;

  &:hover {
    border-bottom-color: ${props => props.color};
  }
`

const Nav = () => {
  const path = window?.location.pathname
  const [active, setActive] = useState(path)

  //const colors = ['#70d5fc', '#f16da5', '#f7966f', '#ffd670', '#e6f06e']

  const handleClick = (link) => {
    setActive(link)
    navigate(link)
  }

  return (
    <>
      <NavContainer>
        <List>
          <Item
            style={active === '/' ? {'border-bottom': '5px solid #70d5fc'} : {}}
            onClick={() => handleClick('/')}
          >
            home
          </Item>
          <Item
            style={active === '/about' ? { 'border-bottom': '5px solid #f16da5' } : {}}
            onClick={() => handleClick('/about')}
          >
            about
          </Item>
          <Item
            style={active === '/posts' ? { 'border-bottom': '5px solid #f7966f' } : {}}
            onClick={() => handleClick('/posts')}
          >
            blog
          </Item>
          <Item>
            <a href={'https://www.colab55.com/@daliannyvieira'} target='_blank'>
              store
            </a>
          </Item>
        </List>
      </NavContainer>
    </>
  )
}

export default Nav
